import React, { useEffect, useState } from "react";
import { Form, Icon, Input, Button, Row, Col, Typography, message } from "antd";
import MyModal from "../../util/WarnModal";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { newPass } from "../../../action/auth";
import { clearErr } from "../../../action/resource";
import { Card } from 'antd';
import useTitle from "../../util/useTitle";

const Singnin = props => {
  const [state, setState] = useState({
    confirmDirty: false
  });

  useTitle("change password");
  const compareToFirstPassword = (rule, value, callback) => {
    const { form } = props;
    if (value && value !== form.getFieldValue("password")) {
      callback("Two passwords that you enter is inconsistent!");
    } else {
      callback();
    }
  };

  const validateToNextPassword = (rule, value, callback) => {
    const { form } = props;
    if (value && state.confirmDirty) {
      form.validateFields(["confirm"], { force: true });
    }
    callback();
  };

  const handleConfirmBlur = e => {
    const { value } = e.target;
    setState({ confirmDirty: state.confirmDirty || !!value });
  };

  const { err } = props;
  useEffect(() => {
    if (Object.keys(props.err).length) {
      message.error("new props");
      MyModal({ data: props.err });
      props.clearErr();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [err]);

  const handleSubmit = e => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      if (!err) {
        props.newPass(values);
      }
    });
  };

  const { getFieldDecorator } = props.form;
  return (
    <Row type="flex" justify="center">
      <Col
        style={{
          maxWidth: "740px",
          width: '90%',
          minWidth: "320px",
          margin: "0 auto ",
          padding: "16px",
          borderRadius: "8px",
          // border: "2px solid #e2e2e2",
        }}
      >
        <Card>
          <Form onSubmit={handleSubmit} className="login-form">
            <Typography.Title level={2} type="secondary">
              Change Password
          </Typography.Title>
            <Form.Item label='Validation Code'>
              {getFieldDecorator("forgotPasswordCode", {
                rules: [
                  { required: true, message: "Please input your validate code!" },
                  {
                    min: 4,
                    message: "min length is 4"
                  }
                ]
              })(
                <Input
                  prefix={
                    <Icon type="unlock" style={{ color: "rgba(255,255,255,.25)" }} />
                  }
                  placeholder="code that send to your email"
                  size="large"
                />
              )}
            </Form.Item>

            <Form.Item label="Email">
              {getFieldDecorator("email", {
                rules: [
                  {
                    type: "email",
                    message: "The input is not valid E-mail!"
                  },
                  {
                    required: true,
                    message: "Please input your E-mail!"
                  }
                ]
              })(
                <Input
                  type="email"
                  size="large"
                  prefix={
                    <Icon type="mail" style={{ color: "rgba(255,255,255,.25)" }} />
                  }
                  placeholder="your email"
                />
              )}
            </Form.Item>
            <Form.Item label="New Passowrd" hasFeedback>
              {getFieldDecorator("password", {
                rules: [
                  {
                    required: true,
                    message: "Please input your password!"
                  },
                  {
                    validator: validateToNextPassword
                  },
                  {
                    min: 6,
                    max: 64,
                    message: "Please input between 6 and 64char"
                  }
                ]
              })(<Input.Password
                prefix={<Icon type="lock" style={{ color: "rgba(255,255,255,.25)" }} />}
                placeholder="new password"
                size="large" />
              )}
            </Form.Item>
            <Form.Item label="Confirm New Password" hasFeedback>
              {getFieldDecorator("confirmPassword", {
                rules: [
                  {
                    required: true,
                    message: "Please confirm your password!"
                  },
                  {
                    validator: compareToFirstPassword
                  }
                ]
              })(
                <Input.Password
                  onBlur={handleConfirmBlur}
                  prefix={<Icon type="lock" style={{ color: "rgba(255,255,255,.25)" }} />}
                  size="large"
                  placeholder="confirm Password"
                />
              )}
            </Form.Item>
            <Form.Item>
              <Link to="/signin">sign in page</Link>
              <br />
              <Button
                type="primary"
                htmlType="submit"
                className="login-form-button"
                loading={props.loading}
                size="large"
              >
                change password
            </Button>
            </Form.Item>
          </Form>
        </Card>
      </Col>
    </Row>
  );
};

const mapStateToProps = state => ({
  auth: state.auth,
  err: state.err,
  loading: state.loading
});

const Signin = Form.create({ name: "signin_form" })(
  connect(
    mapStateToProps,
    { newPass, clearErr }
  )(Singnin)
);

export default Signin;
